.main-cover {
    background: url(../img/bg.jpg) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.main-section {
    min-height: 100vh;
    font-size: 1.3em;
    .role-list {
        font-size: .8em;
        li {
            margin-top: 1.2em;
            margin-bottom: 1.3em;
        }
        span {
            display: block;
        }
    }
}

.social-links {
    font-size: .9em;
}